.button-container{
    padding: 20px;
    justify-content: center;
    margin-bottom: 20px;
    margin-left: 50px;
    width: 50%;
    height: 800px;
    display:flex;
    flex-direction: column;
    background-color: #f5f6fa;
    border-radius: 10px 10px 10px 10px;
    box-shadow: 3px 5px 5px 0px rgba(163,153,163,1);
}

.button-container h2 {
    margin-left: 3dvh;
    font-size: 35px;
}

.button-container label {
    margin-left: 3dvh; 
}

.button-container input {
    margin-left: 3dvh;
    width: 470px;
    height: 40px;
    border-radius: 10px 10px 10px 10px;
    border-color: #f5f6fa;
}

.button-container button {
    margin-left: 3dvh;
    width: 200px;
    height: 40px;
    color: #f5f6fa;
    background-color: #353b48;
    border-radius: 10px 10px 10px 10px;
    border: #576574;
}

.button-container button:hover {
    background-color: #576574;
    border: #576574;
}

.todo-image {
    margin-left: 3dvh;
    margin-top: 20px;
}

