.todo-item{
    width: 500px;
    margin-bottom: 20px;
    border-radius: 10px 10px 10px 10px;
    -moz-border-radius: 10px 10px 10px 10px;
    -webkit-border-radius: 10px 10px 10px 10px;
    box-shadow: 3px 5px 5px 0px rgba(163,153,163,1);
    border: 0px solid #858085;
    background-color: #f5f6fa;
    font-size: 15px;
    font-weight: bold;
    display: flex;
}

.Icon{
    margin-top: 10px;
    margin-left: 10px;
    color: black;
    cursor: pointer;
}

.Icon-check--active{
    color:#4cd137;
}

.todo-item p {
    margin-left: 10px;
}

.TodoItem-p--completed{
    text-decoration: line-through;
}

.Icon-delete{
    margin-top: 2px;
    position: absolute;
    margin-left: 470px;
    cursor: pointer;
    font-size: 25px;
}

.Icon-delete:hover{
    color: red;
}